<template>
  <div class="app-container">
    <!-- 头部搜索 -->
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline>
        <el-form-item prop="materialText">
          <el-input v-model.trim="query.materialText" placeholder="物料编号或名称查询" class="filter-item" style="width: 178px;" @keyup.enter.native="searchInit" />
        </el-form-item>
        <el-form-item prop="status">
          <el-select v-model="query.status" filterable clearable placeholder="状态查询" style="width: 178px;" class="filter-item">
            <el-option
              v-for="item in statusPullMap"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="searchInit"
        >搜索</el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        >重置</el-button>
        <el-button
          class="filter-item"
          type="primary"
          :disabled="!baseInfo.isCanSubmit"
          @click="handleOrderSubmit"
        >提交</el-button>
        <el-button
          class="filter-item fr"
          type="danger"
          style="margin-left: 10px !important;"
          :disabled="!baseInfo.processFlag"
          @click="handleReject"
        >驳回</el-button>
        <el-button
          class="filter-item"
          type="primary"
          @click="handleInspect"
        >开始检验</el-button>
        <el-button
          class="filter-item fr"
          @click="handleGoBack"
        >返回</el-button>
      </el-form>
    </div>
    <el-descriptions :column="3" :label-style="{fontWeight: 900}" :content-style="{paddingRight: '10px',alignItems: 'center'}">
      <el-descriptions-item label="检验单号">
        {{ baseInfo.inspectNo || '---' }}
        <el-link
          v-if="baseInfo.inspectNo"
          style="margin-left: 15px;"
          icon="el-icon-edit"
          :underline="false"
          type="warning"
          @click="modifyInspectNo"
        >修改</el-link>
      </el-descriptions-item>
      <el-descriptions-item label="创建时间">{{ baseInfo.createDate || '---' }}</el-descriptions-item>
      <el-descriptions-item label="接收人">{{ baseInfo.recipientName || '---' }}</el-descriptions-item>
    </el-descriptions>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :index="true"
        :selection="false"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="120"
        :table-column-sort="false"
        :show-operate="false"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <el-button type="primary" :disabled="scope.row.status >= 3" @click="handleSubmit(scope.row)">完成</el-button>
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />

    <!-- 子表 -->
    <edit-dialog
      v-model="dialogVisible"
      :dialog-form="dialogForm"
      :edit-type="editType"
      @confirm="init"
    />

    <!-- 修改检验单号 -->
    <el-dialog
      :title="'修改检验单号'"
      :visible.sync="modifyInspectNoVisible"
      width="30%"
      :before-close="modifyInspectNoCancle"
      :lock-scroll="false"
      :destroy-on-close="true"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      append-to-body
    >
      <el-form ref="modifyInspectNoForm" :model="modifyInspectNoForm" :rules="modifyInspectNoRules" label-width="100px">
        <el-form-item label="检验单号" prop="inspectNo">
          <el-input v-model="modifyInspectNoForm.inspectNo" placeholder="请输入" />
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button :loading="modifyInspectNoLoading" type="primary" @click="modifyInspectNoConfirm('modifyInspectNoForm')">{{ modifyInspectNoLoading ? '提交中' : '确 定' }}</el-button>
        <el-button @click="modifyInspectNoCancle">取 消</el-button>
      </div>
    </el-dialog>
    <!-- 驳回弹窗 -->
    <el-dialog
      :title="'驳回'"
      :visible.sync="rejectVisible"
      width="30%"
      :before-close="rejectCancle"
      :lock-scroll="false"
      :destroy-on-close="true"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      append-to-body
      @open="rejectDlgOpen"
    >
      <el-form ref="rejectForm" :model="rejectForm" :rules="rejectRules" label-width="100px">
        <el-form-item label="原因" prop="rejectReason">
          <el-input v-model="rejectForm.rejectReason" type="textarea" :rows="5" />
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button type="primary" @click="rejectSave('rejectForm')">确 定</el-button>
        <el-button @click="rejectCancle">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import EditDialog from './Dialog'
import { getDetailListApi as getListApi, submitDetailListApi as submitListApi, getInspectNoDetailApi, modifyInspectNoApi, inspectRejectApi, submitInspectResultApi } from '@/apis/modules/in-storage/inspection'
import { statusMap, statusChartMap, statusPullMap } from '@/views/in-storage/inspection/status'

export default {
  // name: 'InStorageInspectionDetail',
  components: {
    EditDialog
  },
  mixins: [paginationPage],
  data() {
    return {
      title: this.$route.meta.title,
      loading: false,
      query: {
        materialText: '',
        status: ''
      },
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'materialCode',
          label: '物料编号',
          align: 'center',
          // width: 120,
          // minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialName',
          label: '物料名称',
          align: 'center',
          minWidth: '200',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialSpec',
          label: '规格型号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialAssessType_dictText',
          label: '评估类型',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'deliveryNumber',
          label: '数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '计量单位',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'applicant',
          label: '申请人',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'status',
          prop: 'status',
          label: '状态',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          formatter: (status) => {
            return statusChartMap[status]
          },
          statusFormatter: (row) => {
            return statusMap[row.status]
          }
        }
      ],
      statusChartMap,
      statusMap,
      columnSets: [],
      dialogForm: {},
      supplierPullList: [],
      editType: 'add',
      dialogVisible: false,
      statusPullMap: statusPullMap,
      baseInfo: {},
      modifyInspectNoVisible: false,
      modifyInspectNoLoading: false,
      modifyInspectNoForm: {
        inspectNo: ''
      },
      modifyInspectNoRules: {
        inspectNo: [
          { required: true, message: '请填写检验单号', trigger: 'blur' }
        ]
      },
      rejectVisible: false,
      rejectRules: {
        rejectReason: [
          { required: true, message: '请填写驳回原因', trigger: 'blur' }
        ]
      },
      rejectForm: {
        rejectReason: ''
      }
    }
  },
  computed: {
    dialogTitle() {
      if (this.editType === 'add') {
        return `新增${this.title}信息`
      } else {
        return `编辑${this.title}信息`
      }
    }
  },
  mounted() {
    this.init()
    this.getInspectNoDetail()
  },
  methods: {
    init(type) {
      console.log('init method')
      this.loading = true
      const params = type ? { ...this.query, deliveryReceiptId: this.$route.params.id } : {
        ...this.query,
        deliveryReceiptId: this.$route.params.id,
        page: {
          ...this.pageInfo
        }
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // 获取检验单详情
    getInspectNoDetail() {
      return new Promise((resolve, reject) => {
        getInspectNoDetailApi({ id: this.$route.params.id })
          .then(res => {
            console.log('物资检验详情---', res)
            const { code, data } = res
            if (!code) {
              this.baseInfo = data
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    // 返回
    handleGoBack() {
      // 删除当前页面路由
      this.$store.dispatch('tagsView/delView', this.$route)
      // 跳转到物资检验页面
      this.$router.push({ name: 'InStorageInspection' })
    },
    // 开始检验
    handleInspect() {
      this.$router.push({
        name: 'InStorageInspectionStart',
        params: {
          id: this.$route.params.id
        }
      })
    },
    // 驳回
    handleReject() {
      this.rejectVisible = true
    },
    rejectDlgOpen() {
      // 重置表单
      this.rejectForm.rejectReason = ''
    },
    // 提交
    handleOrderSubmit() {
      this.$confirm(`确认检验完成, 是否继续?`, '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        cancelButtonClass: 'btn-custom-cancel',
        type: 'warning',
        lockScroll: false
      }).then(() => {
        submitInspectResultApi({ deliveryReceiptId: this.$route.params.id })
          .then(res => {
            const { code } = res
            if (!code) {
              this.$notify.success({
                title: '成功',
                message: '提交成功'
              })
              // 重新拉取最新详情
              // this.getInspectNoDetail()
              // this.init()
              console.log('提交结果', res)
              this.handleGoBack()
            }
          })
          .catch(err => {
            console.log('err', err)
          })
      }).catch(() => {
        this.$notify.info({
          title: '消息',
          message: '已取消操作'
        })
      })
    },
    // 修改检验单号
    modifyInspectNo() {
      this.modifyInspectNoForm.inspectNo = this.baseInfo.inspectNo
      this.modifyInspectNoVisible = true
    },
    modifyInspectNoConfirm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.modifyInspectNoLoading = true
          modifyInspectNoApi({ id: this.$route.params.id, ...this.modifyInspectNoForm })
            .then(res => {
              const { code } = res
              if (!code) {
                this.$notify.success({
                  title: '成功',
                  message: '检验单号修改成功'
                })
                this.getInspectNoDetail()
                this.modifyInspectNoLoading = false
                this.modifyInspectNoVisible = false
              } else {
                this.modifyInspectNoLoading = false
              }
            })
            .catch(err => {
              this.modifyInspectNoLoading = false
              console.log(err)
            })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    modifyInspectNoCancle(done) {
      this.$refs.modifyInspectNoForm.resetFields()
      this.modifyInspectNoVisible = false
      typeof done === 'function' && done()
    },
    // 完成
    handleSubmit(item) {
      this.$confirm(`确认检验完成, 是否继续?`, '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        cancelButtonClass: 'btn-custom-cancel',
        type: 'warning',
        lockScroll: false
      }).then(() => {
        if (!item.id) return
        submitListApi({
          id: item.id
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$message({
              message: '操作成功',
              type: 'success'
            })
            this.init()
          }
        })
      }).catch(() => {
        this.$message({
          message: '已取消操作',
          type: 'info'
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      console.log(row)
      if (column.label === '操作') {
        return false
      }
      // if (row.status < 3) {
      //   this.handleModify({ ...row })
      // }
    },
    refresh() {
      // 更新分页
      this.init('clean')
    },
    handleAdd() {
      this.editType = 'add'
      this.dialogForm = {}
      this.dialogVisible = true
    },
    handleModify(item) {
      this.dialogForm = { ...item }
      this.editType = 'modify'
      this.dialogVisible = true
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    },
    rejectCancle() {
      this.rejectVisible = false
    },
    rejectSave(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          inspectRejectApi({ deliveryReceiptId: this.$route.params.id, ...this.rejectForm })
            .then(res => {
              const { code } = res
              if (!code) {
                // 重新拉取最新详情
                this.getInspectNoDetail()
                this.init()
                // 关闭弹窗
                this.rejectVisible = false
                this.$notify.success({
                  title: '成功',
                  message: '驳回成功'
                })
                this.handleGoBack()
                // this.$store.dispatch('tagsView/delView', this.$route)
                // this.$router.go(-1)
              }
              console.log('驳回结果', res)
            })
            .catch(err => {
              console.log('err', err)
            })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
