<template>
  <el-drawer
    :center="true"
    :append-to-body="true"
    :close-on-press-escape="false"
    :wrapper-closable="false"
    :title="title"
    size="70%"
    direction="btt"
    :visible="value"
    :destroy-on-close="true"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-scrollbar>
      <div style="margin: 0 10px;">
        <divider>物资检验</divider>
        <el-row>
          <el-descriptions :border="true" direction="vertical" :column="7">
            <el-descriptions-item label="物料编号">{{ dialogForm.materialCode }}</el-descriptions-item>
            <el-descriptions-item label="物料名称">{{ dialogForm.materialName }}</el-descriptions-item>
            <el-descriptions-item label="规格型号">{{ dialogForm.materialSpec }}</el-descriptions-item>
            <el-descriptions-item label="评估类型">{{ dialogForm.materialAssessType_dictText }}</el-descriptions-item>
            <el-descriptions-item label="数量">{{ dialogForm.deliveryNumber }}</el-descriptions-item>
            <el-descriptions-item label="计量单位">{{ dialogForm.unitName }}</el-descriptions-item>
            <el-descriptions-item label="状态">
              <el-tag
                :type="statusMap[dialogForm.status]"
                disable-transitions
              >
                {{ statusChartMap[dialogForm.status] }}
              </el-tag>
            </el-descriptions-item>
          </el-descriptions>
        </el-row>

        <el-row>
          <table-com
            :key="refreshKey"
            ref="baseTableRef"
            row-key="uuid"
            :loading="loading"
            :index="true"
            :selection="false"
            :table-data="tableData"
            :column-list="columnList"
            :table-height-type="450"
            :operation-width="100"
            :expand-list="expandList"
            :table-column-sort="false"
            @rowClick="handleRowClick"
            @refresh="searchInit"
          >
            <template slot-scope="scope">
              <el-button type="primary" @click="handleSubmit(scope.row)">保存</el-button>
            </template>
          </table-com>
        </el-row>
        <pagination
          :page.sync="pageInfo.current"
          :limit.sync="pageInfo.size"
          :total="total"
          @pagination="init"
        />
      </div>
    </el-scrollbar>
    <!-- <div class="drawer-footer clearfix">
      <el-button class="fr" @click="beforeClose">取 消</el-button>
      <el-button :loading="loading" type="primary" class="fr" @click="handleConfirm('form')">{{ loading ? '提交中 ...' : '确 定' }}</el-button>
    </div> -->

  </el-drawer>
</template>

<script>
import { v4 as uuidv4 } from 'uuid'
import paginationPage from '@/mixins/paginationPage'
import { getDetailInfoListApi as getListApi, submitDetailInfoListApi as submitListApi } from '@/apis/modules/in-storage/inspection'
import { statusMap, statusChartMap, typePullMap } from '@/views/in-storage/inspection/status'

const INIT_FORM = {
  code: '',
  supplierId: '',
  supplierName: '',
  contact: '',
  phone: '',
  purchaseDate: ''
}

export default {
  name: 'InStorageConsignCom',
  components: {
  },
  mixins: [paginationPage],
  props: {
    value: {
      type: Boolean,
      default: false
    },
    editType: {
      type: String,
      default: 'add'
    },
    dialogForm: {
      type: Object,
      default: () => {
        return { ...INIT_FORM }
      }
    }
  },
  data() {
    return {
      title: '检验',
      refreshKey: new Date().getTime(),
      form: INIT_FORM,
      loading: false,
      rules: {},
      query: {},
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'barCode',
          label: '条码号',
          align: 'center',
          width: 120,
          // minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'select',
          prop: 'inspectType',
          label: '检验类型',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          placeholder: '检验类型',
          optionLabel: 'name', // 下拉选框绑定的 label 字段的key
          optionValue: 'id', // 下拉选框绑定的 value 字段的key
          setDisabled: (row) => {
            return false
          },
          getOptions: (item) => {
            return typePullMap
          }
        },
        {
          module: 'number',
          prop: 'inspectNumberInput',
          label: '检验数量',
          align: 'center',
          minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          disabled: (row) => row.inspectType === 1,
          setMin: (row) => {
            return 0
          },
          setPrecision: (row) => {
            return 3
          }
          // setMax: (row) => {
          //   return row.orderNum
          // },
        },
        {
          module: 'number',
          prop: 'qualifiedNumber',
          label: '合格数量',
          align: 'center',
          minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          setMin: (row) => {
            return 0
          },
          setMax: (row) => {
            return row.inspectNumberInput - row.unqualifiedNumber
          },
          setPrecision: (row) => {
            return 3
          }
        },
        {
          module: 'number',
          prop: 'compromiseNumber',
          label: '让步接收数量',
          align: 'center',
          minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          setMin: (row) => {
            return 0
          },
          setMax: (row) => {
            return row.inspectNumberInput
          },
          setPrecision: (row) => {
            return 3
          }
        },
        {
          module: 'number',
          prop: 'unqualifiedNumber',
          label: '不合格数量',
          align: 'center',
          minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          setMin: (row) => {
            return 0
          },
          setMax: (row) => {
            return row.inspectNumberInput - row.qualifiedNumber
          },
          setPrecision: (row) => {
            return 3
          }
        },
        {
          module: 'input',
          prop: 'remarks',
          label: '备注',
          align: 'center',
          minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      expandList: [],
      dialogVisible: false,
      dialogInfo: {},
      editInfoType: 'add',
      statusChartMap: statusChartMap,
      statusMap: statusMap,
      typePullMap: typePullMap
    }
  },
  computed: {
    dialogTitle() {
      return this.editType === 'add' ? `新增${this.title}` : `编辑${this.title}`
    }
  },
  watch: {
    dialogForm: {
      handler: function(value) {
        if (this.editType === 'add') {
          this.form = { ...INIT_FORM }
        } else {
          this.form = { ...value }
        }
      },
      immediate: true
    }
  },
  methods: {
    init(type) {
      this.loading = true
      return new Promise((resolve, reject) => {
        this.loading = true
        const params = type
          ? {
            ...this.query,
            deliveryMaterialId: this.form.id
          } : {
            ...this.query,
            deliveryMaterialId: this.form.id,
            page: {
              ...this.pageInfo
            }
          }
        getListApi(params).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list.map(item => {
              return {
                ...item,
                inspectNumberInput: item.id === 0 || !item.id ? item.number : item.inspectNumber,
                uuid: uuidv4()
              }
            })
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleConfirm(formName) {
      this.$refs[formName].validate((valid) => {
        this.loading = true
        if (valid) {
          this.$emit('submit', this.form)
          this.beforeClose()
          this.loading = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    handleSubmit(item) {
      const inspectNumberInput = item.inspectNumberInput
      // compromiseNumber
      const total = (item.compromiseNumber + item.qualifiedNumber + item.unqualifiedNumber)
      if (inspectNumberInput < total) {
        this.$notify.info({
          title: '消息',
          message: `合格数量、不合格数量、让步接收数量相加不能超过检验数量`
        })
        return
      }
      if (((item.qualifiedNumber === 0 && item.unqualifiedNumber === 0) || (item.qualifiedNumber === '' && item.unqualifiedNumber === '')) && item.compromiseNumber !== item.inspectNumberInput) {
        this.$notify.info({
          title: '消息',
          message: `合格数量和不合格数量同时为零不能保存`
        })
        return
      }
      if (item.qualifiedNumber === '' || item.unqualifiedNumber === '' || item.compromiseNumber === '') {
        this.$notify.info({
          title: '消息',
          message: `合格数量、不合格数量、让步接收数量不能为空`
        })
        return
      }
      this.$confirm(`确定保存此条信息, 是否继续?`, '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        cancelButtonClass: 'btn-custom-cancel',
        type: 'warning',
        lockScroll: false
      }).then(() => {
        submitListApi({
          ...item,
          inspectNumber: item.inspectNumberInput
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$message({
              message: '操作成功',
              type: 'success'
            })
            this.init()
          }
        })
      }).catch(() => {
        this.$message({
          message: '已取消操作',
          type: 'info'
        })
      })
    },
    onSelectConfirm(list) {
      if (list.length) {
        this.form.supplierId = list[0].id
        this.form.supplierName = list[0].name
      }
    },
    handleAdd() {
      this.editInfoType = 'add'
      this.dialogInfo = {}
      this.dialogVisible = true
    },
    handleModify() {
      this.dialogInfo = { ...this.form }
      this.editInfoType = 'modify'
      this.dialogVisible = true
    },
    handleRowClick({ row, column, cell, event }) {
      console.log(row)
      if (column.label === '操作') {
        return false
      }
      this.rightObj = { ...row }
    },
    refresh() {
      this.init()
    },
    initSelect() {
      this.init()
    },
    beforeClose(done) {
      // this.$refs.form.resetFields()
      this.$emit('input', false)
      this.form = Object.assign({}, { ...INIT_FORM })
      typeof done === 'function' && done()
    }
  }
}
</script>

  <style lang='scss' scoped>
  .container{
  width: 860px;
  margin: 0 auto;
  .left{
    width: 640px;
  }
  .right{
    margin-left: 20px;
    width: 180px;
  }
  }
  ::v-deep:focus {
    outline: 0;
  }
  ::v-deep .el-scrollbar .el-scrollbar__wrap {
    overflow-x: hidden;
  }

  ::v-deep .el-drawer__header {
    margin-bottom: 10px !important;
  }

  .drawer-footer{
    margin-top: 30px;
    margin-right: 30px;
  }
  </style>

